import {Component} from 'react';
import './PageStyle.css';

class Home extends Component{
    render(){
        return(
            <div className='home'>
                <div id='image'></div>
                <div id='welcome'>
                    <p>welcome</p>
                    <p>
                        We are the leader in hardware and software for deep learning!
                        We work on Deep Learning / Machine Learning / Artificial Intelligence / AI. Our goal is to provide intelligent capabilities in a handheld device. In other words, we want to design brains for robots!
                        Research questions: “How do we teach machines to see, hear, predict, understand the world around us?”, “How do we decompose data, speech, text, images, and videos into a learnable hierarchical set of base functions?”, “How do we use these functions to generate action and intelligent behavior in a real-life environment?”, “What hardware can we use to accelerate machine learning and inference?”: these are the scientific questions we are currently after.
                        Other questions and interesting topics can be found here.
                        Research philosophy: We specialize in the use of electrical technologies (deep learning, machine learning, artificial intelligence, AI, microchips and systems of computing devices and sensors) to extend scientific exploratory methods and measurement tools for the endeavor of understanding life and replicating it in engineered systems.
                        Our research foundation is a multi-disciplinary environment encompassing: electrical engineering, biomedical sciences, computational neuroscience, machine learning, computer science, applied mathematics, biology, optics, physics, electrochemistry, to name a few.
                        Our work is applied, and targets applications within a 5-years timeframe. We work on fundamental questions and problem-solving that have a direct applicability to industry and commercial usage.
                        Find us on GitHub.
                        See our most recent technical blog posts on Deep Learning and more here.
                        See our recent software and hardware demonstrations here.
                        Share us on Twitter, Facebook and Google+.
                    </p>
                </div>
            </div>
        );
    }
}

export default Home;