import * as React from 'react';
import ReactMapGL from 'react-map-gl';

function Map() {
    const MAP_TOKEN = 'pk... your mapbox token';

    const [viewport, setViewport] = React.useState({
      latitude: 37.7577,
      longitude: -122.4376,
      zoom: 8
    });
  
    return (
      <ReactMapGL
        {...viewport}
        width="40em"
        height="30em"
        onViewportChange={(viewport) => setViewport(viewport)}
      />
    );
  }

export default Map;