import {Component} from 'react';
import './NavStyle.css';

class Nav extends Component{
    render(){
        return(
            <div className='nav'>
                <button id='nav-menu' onClick={() => this.props.changeMenu('Home')}>Home</button>
                <button id='nav-menu' onClick={() => this.props.changeMenu('People')}>People</button>
                <button id='nav-menu' onClick={() => this.props.changeMenu('Research')}>Research</button>
                <button id='nav-menu' onClick={() => this.props.changeMenu('Publication')}>Publication</button>
                <button id='nav-menu' onClick={() => this.props.changeMenu('Software')}>Software</button>
                <button id='nav-menu' onClick={() => this.props.changeMenu('Contact')}>Contact</button>
            </div>
        );
    }
}

export default Nav;